submit\"\n              >\n                <div class=\"text-center mb-11\">\n                  <div class=\"text-gray-500 fw-semibold fs-6\" v-if=\"$page.props.settings.logo.main\">\n                    <img :src=\"$page.props.settings.logo.main.original_url\" class=\"img-fluid mw-250px\" v-if=\"$page.props.settings.logo.main.original_url\">\n                  </div>\n                </div>\n                <div class=\"d-flex flex-column mb-8\">\n                  <span class=\"p-input-icon-left\">\n                    <i class=\"bi bi-envelope\" />\n                    <InputText v-model=\"form.email\" type=\"email\" class=\"w-100\" placeholder=\"E-posta\"/>\n                  </span>\n                </div>\n\n                <div class=\"d-flex flex-column mb-3\">\n                  <span class=\"p-input-icon-left\">\n                    <i class=\"bi bi-key\" />\n                    <InputText v-model=\"form.password\" type=\"password\" class=\"w-100\" placeholder=\"Şifre\"/>\n                  </span>\n                </div>\n                <div\n                  class=\"d-flex flex-stack flex-wrap gap-3 fs-base fw-semibold mb-8\"\n                >\n                  <div></div>\n\n                  <Link v-if=\"canResetPassword\" :href=\"route('password.request')\" class=\"link-primary\">\n                    Şifremi Unuttum\n                  </Link>\n                </div>\n                <div class=\"d-grid mb-10\">\n                  <button class=\"btn btn-primary\" type=\"submit\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">Giriş Yap</button>\n                </div>\n\n                <FormErrors :form=\"form\" />\n\n              </form>\n            </div>\n\n            <div class=\"d-flex flex-stack\">\n\n            </div>\n            <!--end::Footer-->\n          </div>\n          <!--end::Content-->\n        </div>\n        <!--end::Wrapper-->\n      </div>\n      <!--end::Body-->\n    </div>\n    <!--end::Authentication - Sign-in-->\n  </div>\n  <!--end::Root-->\n</template>\n\n<script>\nimport GuestLayout from \"../../Layouts/GuestLayout\";\nexport default {\n  layout: GuestLayout,\n};\n</script>\n\n<style>\nbody {\n  background-image: url(\"/dmn/media/auth/bg10.jpeg\");\n}\n\n[data-bs-theme=\"dark\"] body {\n  background-image: url(\"/dmn/media/auth/bg10-dark.jpeg\");\n}\n</style>\n\n<script setup>\nimport { useForm, Link } from \"@inertiajs/vue3\";\nimport InputText from 'primevue/inputtext';\nimport Password from 'primevue/password';\nimport FormErrors from '../../Components/FormErrors';\n\ndefineProps({\n  canResetPassword: Boolean,\n  status: String,\n});\n\nconst form = useForm({\n  email: \"\",\n  password: \"\",\n});\n\nconst submit = () => {\n  form.post(route(\"login\"), {\n    onFinish: () => form.reset(\"password\"),\n  });\n};\n</script>"],"sourceRoot":""}]);
